import { Component } from '@angular/core';
import { ActiveModalRef } from '@bmc-ux/adapt-angular';
import { IEditRecordsActionResults } from '../interfaces/edit-records-action-results.interface';

@Component({
  selector: 'rx-edit-records-result-modal',
  styleUrls: ['./result-modal.scss'],
  templateUrl: './result-modal.html'
})
export class ResultModalComponent {
  data;

  constructor(public activeModelRef: ActiveModalRef) {
    this.data = this.activeModelRef.getData();
  }

  close() {
    this.activeModelRef.close();
  }

  hasActionResultDetails(data: IEditRecordsActionResults): boolean {
    return ['warningCount', 'errorCount', 'infoCount', 'successCount'].some((prop) => data.summary[prop]);
  }
}
